 <template>
   <div class="skill md-primary has-ripple">
     <div class="row main" :class="[classes]" @click.stop="onClick">
       <div class="col-sm-4 name-container">
         <div class="expand-indicator rotatable" :class="[expandIndicatorClasses]" v-if="expandable">
           <slot name="expand-indicator">
             <md-icon>keyboard_arrow_down</md-icon>
           </slot>
         </div>

          <h3 class="name" v-bind:style="nameStyles">
            {{ name }}
          </h3>
       </div>
       <div class="col-sm-8 rating-container">
          <md-rating-bar class="md-primary rating" ref="rating" v-model="rating" :md-num-stars="numStar" disabled></md-rating-bar>
       </div>
     </div>

     <transition name="expand-y">
       <div class="expand-container" :style="nameStyles" v-show="expanded || !expandable">
         <slot></slot>
       </div>
     </transition>
   </div>
 </template>

 <style lang="scss" scoped>
 @import "../../node_modules/vue-material/src/core/stylesheets/variables";
 @import "./css/media-queries-breakpoints";
 @import "./css/utils";

 $animation-duration: 1.0s;
 $expanded-height: 1000px;

 .skill{
   display: block;
   padding: 4px 4px;
   cursor: default;
   border-radius: 2px;

   &:hover:not(:disabled){
      background: rgba(50, 50, 50, .1);
      .main{
        cursor: pointer;
        .md-icon{
          cursor: pointer;
        }
      }
   }

   @include xs{
     width: 100%;
     margin-left: auto;
     margin-right: auto;

     .main{
       width: fit-content;
       margin-left: auto;
       margin-right: auto;
     }
   }

   .name-container{
     display: flex;
     flex-direction: row;

     .expand-indicator{
       margin: auto 0px;
     }
   }

   .name, .md-rating-bar{
     display: inline-flex;
   }

   .name{
     margin: 0em 1em 0em 0em;
     font-weight: normal;
   }

   .name,
   .md-rating-bar.rating,
   .expand-indicator{
     user-select: none;
   }

   .md-rating-bar.rating{
     margin-top: auto;
     margin-bottom: auto;

     @include xs{
         margin-left: 4em;
     }

     .md-back-stars,
     .md-front-stars{
       display: inline-flex !important;
     }
   }

   .rotatable {
     transition: $swift-ease-out;
     transition-timing-function: ease;
     transition-delay: 0s;
   }

   .rotate-clockwise{
     transform: rotate(180deg);
   }

   .expand-y-enter-active {
     overflow: hidden;
     animation: expand-in $swift-ease-in-duration;
     animation-timing-function: $swift-ease-in-timing-function;
   }
   .expand-y-leave-active {
     overflow: hidden;
     animation: expand-out $swift-ease-out-duration;
     animation-timing-function: $swift-ease-out-timing-function;
   }
   @keyframes expand-in {
     0% {
       max-height: 0px;
     }
     100% {
       max-height: $expanded-height;
     }
   }
   @keyframes expand-out {
     0% {
       max-height: $expanded-height;
     }
     100% {
       max-height: 0px;
     }
   }
 }
 </style>

 <script>
 export default{
   name: 'skill',
   props: {
     rating: {
       type: Number,
       default: 0,
     },
     name: {
       type: String,
       default: '',
     },
     break: {
       type: String,
       default: 'auto',
     },
     expandable: Boolean,
   },
   data: function(){
     return {
       expanded: false,
       numStar: 5,
     };
   },
   mounted: function(){
     window.addEventListener('resize', this.onResize.bind(this));
   },
   beforeDestroy: function(){
     window.removeEventListener('resize', this.onResize);
   },
   computed: {
     classes: function(){
       let classes = {};

       let validsRowEdH = ['sm', 'xs'];
       let rowEqH = 'row-eq-h';
       if(validsRowEdH.indexOf(this.break) > -1)
         rowEqH += `-${this.break}`;
       classes[rowEqH] = true;

       return classes;
     },
     expandIndicatorClasses: function(){
       return {
         'rotate-clockwise': this.expanded,
       };
     },
     nameStyles: function(){
       return {
         marginLeft: `${1.4 * !this.expandable}em`,
       };
     },
   },
   methods: {
     onClick: function(){
       this.expanded = !this.expanded;
     },
     onResize: function(){
       if(this.$refs.rating){
         console.log(this.$refs.rating.$el);
       }
     },
   },
 };
 </script>



