<template>
  <div class="resume">
    <header-info :background-color="primaryColor"
                 :infos="infos"
                 :title="title">
      <img slot="left-side" src="../assets/profile-picture.jpg" />
    </header-info>
    <div class="body">
      <category :title="'RESUMO'">
        <p>
          {{ resumeText }}
        </p>
      </category>

      <category :title="'FORMAÇÃO'" class="degree">
        <list-topics :title="academicQualification.title"
                     :topics="academicQualification.topics" />

        <list-topics :title="additionalQualification.title"
                     :topics="additionalQualification.topics" />
      </category>

      <category :title="'COMPETÊNCIAS'" class="degree" v-if="skills.length">
        <div class="skills-container">
          <skill-recursive v-for="skill in skills" :skill="skill" :break-point="'sm'"></skill-recursive>
        </div>
      </category>

      <category :title="'PROJETOS'">
        <project-resume v-for="project in projects"
                        :name="project.name"
                        :infos="project.infos"
                        :resume="project.resume"
                        :quickLink="project.link"
                        :key="project.id">

                        <expandable-box v-if="project.additional">
                          <div class="list-link">
                            <p>
                              {{ project.additional.listLinks.resume }}
                            </p>

                            <md-button v-for="link in putID(reverse(project.additional.listLinks.links))" class="md-primary button-link"
                                       :href="link.href" :target="'_blank'" :key="link.id">
                                       {{ link.name }}
                            </md-button>

                          </div>
                        </expandable-box>

        </project-resume>
      </category>

      <category :title="'PORTFÓLIO'">
        <project-resume v-for="project in portfolio"
                        :name="project.name"
                        :infos="project.infos"
                        :resume="project.resume"
                        :quickLink="project.link"
                        :key="project.id">

        </project-resume>
      </category>

      <category :title="'INFORMAÇÕES \n ADICIONAIS'">
        <p v-for="additional in additionalInformations"
           class="addtionalInformation">
          {{ additional.info }}
        </p>
      </category>
    </div>
  </div>
</template>

<script>
import Header from './Header';
import Category from './Category';
import ListTopics from './ListTopics';
import ProjectResume from './ProjectResume';
import ExpandableBox from './ExpandableBox';
import SkillRecursive from './SkillRecursive';
import strings from '../strings';
import resumeData from '../resume-data';

export default{
  name: 'resume',
  components: {
    'header-info': Header,
    'category': Category,
    'list-topics': ListTopics,
    'project-resume': ProjectResume,
    'expandable-box': ExpandableBox,
    'skill-recursive': SkillRecursive,
  },
  data: function(){
    return {
      primaryColor: '#FEC00C',
      resumeData: resumeData,
    };
  },
  computed: {
    infos: function(){
      var infos = [];
      var resume = this.resumeData;

      infos.push(strings.infos.resume(resume.birth.place,
                 resume.maritalStatus, this.getYears()));

      infos.push(strings.infos.address(resume.address.city, resume.address.state.acronym,
                 resume.address.country));

      infos.push(strings.infos.email(resume.email));
      infos.push(strings.infos.github(resume.github));

      return infos;
    },
    title: function(){
      return strings.infos.name(this.resumeData.name);
    },
    resumeText: function(){
      return strings.resumeText(this.resumeData.resumeText);
    },
    academicQualification: function(){
      return this.mixinQualification(strings.academicQualification,
                                this.resumeData.academicQualifications);
    },
    additionalQualification: function(){
      return this.mixinQualification(strings.additionalQualification,
                                this.resumeData.additionalQualifications);
    },
    projects: function(){
      return this.projectsMixin(this.resumeData.projects);
    },
    portfolio: function(){
      return this.projectsMixin(this.resumeData.portfolio);
    },
    additionalInformations: function(){
      return this.resumeData.additionalInformations;
    },
    skills: function(){
      if(!this.resumeData.skills)
        return [];

      let skills = this.resumeData.skills.map(function(skill){
        return {
          name: strings.skill.name(skill.name),
          rating: parseFloat(skill.rating),
          text: strings.skill.text(skill.text || ''),
          sub: skill.sub,
        };
      });

      return skills;
    },
  },
  methods: {
    getYears: function(){
      var yearsOld = new Date() - this.resumeData.birth.date;
      yearsOld /= 1000 * 60 * 60 * 24 * 365;
      return Math.floor(yearsOld);
    },
    mixinQualification: function(qualificationsStrings, qualifications){
      var result = {};
      result.title = qualificationsStrings.title();
      result.topics = qualifications.map(function(e){
        return { text: qualificationsStrings.topic(e) };
      });

      return result;
    },
    projectsMixin: function(projectsData){
      if(!projectsData)
        return [];

      let projects = projectsData.map(function(project, i){
        var infos = [];

        for(const prop in project.infos){
          if(strings.project.infos[prop]){
            let info = { text: strings.project.infos[prop](project.infos[prop]) };
            infos.push(info);
          }
          else
            throw new Error(`Property ${prop} is not found in strings.js`);
        }

        if(project.link)
          var link = {
            text: strings.project.textLinkButton(),
            href: project.link,
          };

        let resume = project.resume;
        if(typeof resume === 'string')
          resume = [resume];

        resume = resume.map(function(p){
          return strings.project.resume(p);
        });

        return {
          name: strings.project.name(project.name),
          infos: infos,
          link: link,
          resume: resume,
          additional: project.additional,
          id: i,
        };
      });

      return projects;
    },
    onSkillClick: function(){
    },
    reverse: function(value){
      return value.slice().reverse();
    },
    putID: function(value){
      value.forEach(function(e, i){
        e.id = i;
      });
      return value;
    },
  },
};

</script>

<style lang="scss">
@import "./css/media-queries-breakpoints";
@import "./css/utils";

.resume{
  p{
    margin: 0;
  }
  a:not(.md-button):not(.show-more){
    color: #2c3e50 !important;
  }
  .category{
    margin-top: 2em;
  }
  .list-topics:not(:last-child){
    margin-bottom: 1.3em;
  }
  .addtionalInformation{
    margin-bottom: 0.8em;
    text-align: justify;
  }
  .header-info{
    $img-height-original: 170px;
    $increased-scale: 1.15;
    $margin-fix: 40px;

    height: auto;
    margin-left: $margin-fix;
    margin-right: $margin-fix;

    /*fine adjustiment*/
    $img-y-adjustment: 0px;
    $img-y-adjustment-sm: -20px;

    @include border-radius;
    @include xs{
        padding-bottom: 0.2em;
    }

    .left-content, .right-content{
      position: relative;
      left: -$margin-fix/3;
      @include xs{
          left: 0px;
      }
    }

    img{
      height: $img-height-original !important;
      position: relative;
      transform: scale($increased-scale);
      top: $img-y-adjustment;
      @include border-radius;
      @include xs {
        margin-right: auto;
        margin-left: auto;
        display: block;
        top: $img-y-adjustment-sm;
      }
    }
    .left-side{
      padding-right: 1em;
    }
  }

  .category{
    .right-side{
      padding-top: 5px;
      margin-left: 1.5em;
      @include xs{
        margin-left: 0px;
      }
    }
  }
  .list-link{
    .button-link{
      display: block;
    }
    .button-link:not(:first-of-type){
      text-align: left;
    }
  }

  .skills-container{
    margin: 0px auto;
    width: 100%;

    .skill{
      .name{
        text-align: start;
        text-transform: uppercase;
        padding-left: .5em;
        margin: auto 0px;
        display: block;
        @include xs{
          text-align: left;
        }
      }
      .rating-container{
        align-items: flex-end;
        .rating{
          margin-right: 1em;
          padding-right: 0px;
        }
      }
    }
  }
}
</style>



