<template>
  <div class="list-topics">
    <p if="title">
      {{ title }}
    </p>
    <ul>
      <li v-for="topic in topics">
        {{ topic.text }}
        <list-topics if="topic.sub"></list-topics>
      </li>
    </ul>
  </div>
</template>

<style scoped>
p{
  font-weight: bold;
  margin: 0;
}

ul{
  margin: 0.3em 0em 0em 0em;
}
</style>

<script>

export default{
  name: 'list-topics',
  props: {
    topics: {
      type: Array,
      'default': function(){
        return [];
      },
    },
    title: {
      type: String,
    },
  },
  data: function(){
    return {};
  },
};

</script>



