export default {
  methods: {
    mdLinkToAlink: function(text, options){
      let op = options || {};
      let targetLink = op.target === undefined ? '' : op.target;
      let titleLink = op.title === undefined ? '' : op.title;
      return text.replace(/\[(.+)\]\((.+)\)/g, `<a href="$2" target="${targetLink}" title="${titleLink}">$1</a>`);
    },
  },
};



