 <template>
   <div class="expandable-box">
     <md-button class="md-icon-button md-primary" @click.native="onButtonClick">
       <md-icon class="rotatable" :class="[iconRotateClass]">add</md-icon>
     </md-button>
     <div class="box" :class="classes">
       <slot></slot>
     </div>
   </div>
 </template>

 <style lang="scss" scoped>
 $stroke-width: 2px;
 $line: $stroke-width solid #83858B;
 $expanded-height: 1000px;
 $animation-duration: 1.0s;
 $animation-duration-fix: 0.5;

 @mixin expanded {
   max-height: $expanded-height;
   border-bottom: $line;
 }

 @mixin collapsed {
   max-height: 0px;
 }

 @keyframes expand {
   0% {
     @include collapsed;
     border-bottom: $line;
   }
   100% {
     @include expanded;
   }
 }

 @keyframes collapse {
   0% {
     @include expanded;
   }
   100% {
     @include collapsed;
     border-bottom: $line;
   }
 }

 .expandable-box{
   .box{
     overflow: hidden;
     display: inline-block;
     margin-top: 19px;
     margin-left: -6px;
     width: 80%;
     border-top: $line;
   }

   .collapsed{
     @include collapsed;
   }
   .expanded{
     @include expanded;
   }

   @mixin animation-class-mixins{
     animation-duration: $animation-duration;
     animation-delay: 0s;
     animation-direction: normal;
     animation-iteration-count: 1;
     animation-fill-mode: none;
     animation-play-state: running;
   }

  .expand {
    animation-name: expand;
    animation-timing-function: cubic-bezier(.61,.13,.36,.91);
    @include animation-class-mixins;
  }
  .collapse {
    animation-name: collapse;
    animation-timing-function: cubic-bezier(0,1,.38,.95);
    @include animation-class-mixins;
  }

  .rotatable {
    transition: transform $animation-duration;
    transition-timing-function: ease;
    transition-delay: 0s;
  }

  .rotate-counterclockwise{
    transform: rotate(45deg);
  }
 }
 </style>

 <script>
 export default{
   name: 'expandable-box',
   data: function(){
     return {
       isExpanded: false,
       inAnimation: false,
       classes: {
         expand: false,
         collapse: false,
         expanded: false,
         collapsed: true,
       },
       iconRotateClass: {
         'rotate-counterclockwise': false,
       },
     };
   },
   mounted: function(){
     var listener = function(){
       this.inAnimation = false;
       this.classes.expanded = this.isExpanded;
       this.classes.collapsed = !this.isExpanded;
     }.bind(this);
     this.$el.addEventListener('animationend', listener, false);
   },
   methods: {
     onButtonClick: function(){
       this.inAnimation = true;
       this.classes.collapsed = !this.inAnimation;
       this.classes.expanded = !this.inAnimation;

       this.isExpanded = !this.isExpanded;
       this.classes.expand = this.isExpanded;
       this.classes.collapse = !this.isExpanded;
       this.iconRotateClass['rotate-counterclockwise'] = this.isExpanded;
     },
   },
 };
 </script>



