<template>
 <div class="skill-recursive">
   <skill :name="skill.name" :rating="skill.rating" :break="breakPoint" :expandable="!!skill.text || !!skill.sub">
     <div v-show="skill.text" class="text">
      <p>{{ skill.text }}</p>
     </div>
     <skill-recursive v-for="subSkill in skill.sub" class="sub-skill" :skill="subSkill" :breakPoint="breakPoint"></skill-recursive>
   </skill>
 </div>
</template>

<style lang="scss">
@import "./css/media-queries-breakpoints";

$indentation: 1em;

.skill-recursive{
  @include xs{
    div[class*="col-"]{
      padding-left: 0px;
      padding-right: 0px;

    }
  }

  .sub-skill{
    .skill{
      .main{
        padding-left: $indentation;
        margin-right: -19px;

        @include xs{
          padding-left: 1em + $indentation;
        }
      }
      .expand-container{
        @include sm{
          padding-left: 1em+$indentation;

          p{
            text-indent: 1.5em;
          }
        }

      }
    }
  }
}
</style>

<script>
import Skill from './Skill';

export default{
  name: 'skill-recursive',
  components: {
    'skill': Skill,
  },
  props: {
    skill: Object,
    breakPoint: {
      type: String,
      default: 'auto',
    },
  },
  data: function(){
    return {};
  },
};
</script>



