<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import Resume from './components/Resume';
import resumeData from './resume-data';

export default {
  name: 'app',
  components: {
    'resume': Resume,
  },
  data: function(){
    return {
      resumeData: resumeData,
    };
  },
};
</script>

<style lang="scss">
@import "./components/css/media-queries-breakpoints";
@import "./components/css/utils";

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 850px;
  min-height: 100%;
  padding-top: 40px;
  padding-right: 25px;
  padding-left: 25px;
  font-size: 15px;
  background: rgba(255,255,255,.95);
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.2);
  @include xs{
    @include break-all;
    padding-right: 0px;
    padding-left: 0px;
  }
}
body{
  margin: 0;
  background: #999 !important;
  height: 100%;
}
</style>



