<template>
  <div class="container-fluid category">
    <div class="row">
      <div class="col-sm-4 left-side">
        <div class="title" v-for="line in titleFormated">
          {{ line }}
        </div>
      </div>
      <div class="col-sm-7 right-side">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
@import "./css/media-queries-breakpoints";

.title{
  text-align: right;
  font-size: 1.8em;
  color: #777;
  @include xs{
    text-align: center;
    margin-bottom: 0.2em;
  }
  @include sm{
    font-weight: normal;
  }
  @include md{
    font-weight: normal;
  }
}
.right-side{
  text-align: justify;
  margin-left: 1.5em;
}
</style>

<script>
export default{
  name: 'category',
  props: {
    title: {
      type: String,
      'default': 'TITLE',
    },
  },
  data: function(){
    return {};
  },
  computed: {
    titleFormated: function(){
      return this.title.split('\n');
    },
  },
};
</script>



