import Vue from 'vue';
import VueRouter from 'vue-router';
// vue-material imports
import VueMaterial from 'vue-material';
import 'vue-material/dist/components/mdCore/index.css';
import 'vue-material/dist/components/mdButton/index.css';
import 'vue-material/dist/components/mdIcon/index.css';
import 'vue-material/dist/components/mdRatingBar/index.css';

import './bootstrap/index.less';
import './override-vue-material-mdCore.scss';// remove inconvenience style
import App from './App';
import Routes from './routes';

Vue.config.productionTip = false;

// vue material setup
Vue.use(VueMaterial.MdCore);
Vue.use(VueMaterial.MdButton);
Vue.use(VueMaterial.MdIcon);
Vue.use(VueMaterial.MdRatingBar);
Vue.material.registerTheme('default', {
  primary: {
    color: 'amber',
    hue: 600,
  },
  accent: 'orange',
  warn: 'red',
  background: 'white',
});

Vue.use(VueRouter);

const router = new VueRouter({
  routes: Routes,
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router: router,
});



