<template>
  <div class="container-fluid header-info" :style="style">
  	<div class="row">
  	  <div class="col-sm-4 left-side">
        <div class="left-content">
          <slot name="left-side">
            <img src="../assets/profile-picture.png"/>
          </slot>
        </div>
      </div>
      <div class="col-sm-7 right-side">
        <div class="right-content">
    	    <slot name="right-side">
            <div class="title">{{ title }}</div>
            <div class="infos">
              <div class="info" v-for="info in infos" v-html="mdLinkToAlink(info, {target: '_blank'})"></div>
            </div>
          </slot>
        </div>
  	  </div>
  	</div>
  </div>
</template>

<script>
import LinkMixin from './LinkMixin';

export default{
  name: 'header-info',
  mixins: [LinkMixin],
  props: {
    backgroundColor: {
      type: String,
      'default': 'orange',
    },
    title: {
      type: String,
      'default': 'Title',
    },
    infos: {
      type: Array,
      'default': function(){
        return ['info1', 'info2'];
      },
    },
    profilePicture: {
      type: String,
      'default': './assets/profile-picture.png',
    },
  },
  components: {},
  data: function(){
    return {};
  },
  computed: {
    style: function(){
      return {
        'background-color': this.backgroundColor,
      };
    },
  },
};

</script>

<style lang="scss" scoped>
@import "./css/media-queries-breakpoints";
@import "./css/utils";

.header-info{
  img{
    height: auto;
  }
  .right-side{
    text-align: left;
    margin-left: 1.5em;
    @include xs{
      margin-left: 0px;
    }
  }
  .left-side{
    text-align: right;
  }
  .title{
    font-weight: bold;
    margin-bottom: 0.5em;
    font-size: 2em;
    @include xs{
        text-align: center;
    }
  }

  /*center infos*/
  .right-content, .left-content, .infos{
    @include xs{
      margin-right: auto;
      margin-left: auto;
      display: block;
      @extend .fitcontent-xs;
    }
  }
}

</style>



